import React, { useState, useEffect } from "react";
import { StyledHeader } from "./Header.styles";
import { Link } from "react-router-dom";
import { getCurrentDate } from "../../api";
import WeatherIcon from "../Weather/WeatherIcon";
import SearchBar from "../SearchBar/SearchBar";

const Title = () => {
  const [date, setDate] = useState("");

  useEffect(() => {
    const getDate = async () => {
      const date = await getCurrentDate();
      setDate(date);
    };

    getDate();
  }, []);

  return (
    <StyledHeader>
      <div className="header">
        <div className="lingo">
          <Link to="/3d-news/">
            <button>U.S.</button>
          </Link>
          <Link to="/3d-news/france">
            <button>FRANCE</button>
          </Link>
          <Link to="/3d-news/spain">
            <button>ESPAÑA</button>
          </Link>
          <Link to="/3d-news/ae">
            <button>العربية</button>
          </Link>
        </div>
        <div className="title">
          <h4>{date}</h4>
          <Link to="/">
            <h1>THE LA TIMES</h1>
          </Link>
          <h4>
            <WeatherIcon />
          </h4>
        </div>
        <SearchBar />
        <div className="categories">
          <Link to="/3d-news/business">
            <button id="business">business</button>
          </Link>
          <Link to="/3d-news/et">
            <button id="entertainment">entertainment</button>
          </Link>
          <Link to="/3d-news/general">
            <button id="general">general</button>
          </Link>
          <Link to="/3d-news/health">
            <button id="health">health</button>
          </Link>
          <Link to="/3d-news/science">
            <button id="sciences">science</button>
          </Link>
          <Link to="/3d-news/sports">
            <button id="sports">sports</button>
          </Link>
          <Link to="/3d-news/tech">
            <button id="technology">technology</button>
          </Link>
        </div>
      </div>
    </StyledHeader>
  );
};

export default Title;
